import OptionsPanel from "./OptionsPanel.js"

export default class CharacterEdgeStyle extends OptionsPanel {

  constructor(props, context) {
    super(props, context)
    this.listOptions = [
      {label: "None", value: "text-shadow: 0px 0px 0px"},
      {label: "Depressed", value: "text-shadow: 0px 1px 0px"},
      {label: "Left Drop Shadow", value: "text-shadow: -3px 3px 2px"},
      {label: "Raised", value: "text-shadow: 0px 1px 1px"},
      {label: "Right Drop Shadow", value: "text-shadow: 3px 3px 2px"},
      {label: "Uniform", value: "text-shadow: 0px 0px 4px"}
    ]
    
    this.state.title = "Character Edge Style" // TODO: This needs to be localized
    this.setting = "edgeType"
    this.select()
  }
}
