import OptionsPanel from "./OptionsPanel.js"

export default class FontFamily extends OptionsPanel {

  constructor(props, context) {
    super(props, context)
    this.listOptions = [
      {label:"Monospaced Serif", value: "'Courier New', Courier, 'Nimbus Mono L', 'Cutive Mono', monospace"},
      {label:"Proportional Serif", value: "'Times New Roman', Times, Georgia, Cambria, 'PT Serif Caption', serif"},
      {label:"Monospaced Sans-Serif", value: "'Deja Vu Sans Mono', 'Lucida Console', Monaco, Consolas, 'PT Mono', monospace"},
      {label:"Proportional Sans-Serif", value: "Roboto, 'Arial Unicode Ms', Arial, Helvetica, Verdana, 'PT Sans Caption', sans-serif"},
      {label:"Casual", value: "'Comic Sans MS', Impact, Handlee, fantasy"},
      {label:"Cursive", value: "'Monotype Corsiva', 'URW Chancery L', 'Apple Chancery', 'Dancing Script', cursive"},
      {label:"Small Capitals", value: "'Arial Unicode Ms', Arial, Helvetica, Verdana, 'Marcellus SC', sans-serif; font-variant: small-caps"}
    ]

    this.state.title = "Font Family" // TODO: This needs to be localized
    this.setting = "fontFamily"
    this.select()
  }
}
