import OptionsPanel from "./OptionsPanel.js"

export default class FontSize extends OptionsPanel {

  constructor(props, context) {
    super(props, context)
    this.listOptions = [
      {label:"70%", value: "x-small"},
      {label:"100%", value: "small"},
      {label:"150%", value: "medium"},
      {label:"200%", value: "large"},
      {label:"300%", value: "x-large"}
    ]

    this.state.title = "Font Size" // TODO: This needs to be localized
    this.setting = "size"
    this.select()
  }
}
