import OptionsPanel from "./OptionsPanel"
import Color from "./Color"

export default class BackgroundColor extends OptionsPanel {

  constructor(props, context) {
    super(props, context)
    this.listOptions = [
      {label: "White", value: new Color(255,255,255)},
      {label: "Yellow", value: new Color(255,255,0)},
      {label: "Green", value: new Color(0,128,0)},
      {label: "Cyan", value: new Color(0,255,255)},
      {label: "Blue", value: new Color(0,0,255)},
      {label: "Magenta", value: new Color(255,0,255)},
      {label: "Red", value: new Color(255,0,0)},
      {label: "Black", value: new Color(0,0,0)}
    ]

    this.state.title = "Background Color" // TODO: This needs to be localized
    this.setting = "backgroundColor"
    this.select()
  }
}
