import OptionsPanel from "./OptionsPanel.js"

export default class ScrollType extends OptionsPanel {

  constructor(props, context) {
    super(props, context)
    this.listOptions = [
      {label:"Pop-out", value: "popout"},
      {label:"Roll-on", value: "rollon"},
      {label:"Paint-on", value: "painton"}
    ]

    this.state.title = "Scroll Type" // TODO: This needs to be localized
    this.setting = "scroll"
    this.select()
  }
}
